<template>
  <div class="ImgCanvas">
    <div class="box" v-for="(item, index) in images" :key="item.imageUrl">
      <canvas :id="`mycanvas-${index + 1}`" width="800" height="1200"></canvas>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "ImgCanvas",
  data() {
    return {};
  },
  props: {
    images: {
      type: Array,
      default: () => [],
    },
  },
  mounted() {
    this.startDoDraw(this.images);
  },
  watch: {
    images(newVal, oldVal) {
      if (newVal !== oldVal) {
        newVal.forEach((item, index) => {
          if (index >= 5) {
            this.doDraw(item.imgUrl, "mycanvas-" + (index + 1));
          }
        });
      }
    },
  },
  methods: {
    startDoDraw(images) {
      images.forEach((item, index) => {
        this.doDraw(item.imgUrl, "mycanvas-" + (index + 1));
      });
    },
    doDraw(imageUrl, name) {
      var that = this;
      this.$nextTick(() => {
        //  获取canvas
        var canvas = document.getElementById(name);
        if (!canvas) {
          return false;
        } else {
          //  可以理解为一个画笔，可画路径、矩形、文字、图像
          var context = canvas.getContext("2d");
          var img = new Image();
          img.src = imageUrl;
          //  加载图片
          img.onload = function () {
            if (img.complete) {
              //  根据图像重新设定了canvas的长宽
              img.width = 620.5;
              img.height = 877.5;
              var ratio = that.getPixelRatio(context);
              canvas.setAttribute("width", img.width);
              canvas.setAttribute("height", img.height);
              //  绘制图片
              context.drawImage(
                img,
                0,
                0,
                img.width * ratio,
                img.height * ratio
              );
            }
          };
        }
      });
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
  },
};
</script>
  <style scoped>
.ImgCanvas {
  max-width: 1000px;
  width: 100%;
  height: auto;
  background: #f5f5f5;
  overflow-x: hidden;
  margin: 0 auto;
  padding-top: 80px;
}
.box {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}
</style>
  