<template>
  <div class="PdfCanvas">
    <div :style="{ width: pdf_div_width, margin: '0 auto' }">
      <canvas
        v-for="page in pdf_pages"
        :id="'the_canvas' + page"
        :key="page"
      ></canvas>
    </div>
  </div>
</template>
  
<script>
let PDFJS = require("pdfjs-dist");
PDFJS.GlobalWorkerOptions.workerSrc = require("pdfjs-dist/build/pdf.worker.entry.js");

export default {
  name: "PdfCanvas",
  data() {
    return {
      pdf_scale: 1.0, //pdf放大系数
      pdf_pages: [],
      pdf_div_width: "",
      pdf_src: "/static/pdfs/01.pdf",
    };
  },
  props: {
    images: {
      type: Array,
      default: () => [],
    },
  },
  mounted() {
    this.get_pdfurl();
  },
  watch: {
    images(newVal, oldVal) {
      if (newVal !== oldVal) {
        this._loadFile(this.pdf_src, newVal.length);
      }
    },
  },
  methods: {
    get_pdfurl() {
      this._loadFile(this.pdf_src, this.images.length);
    },
    _loadFile(url, numPages) {
      //初始化pdf
      let loadingTask = PDFJS.getDocument(url);
      loadingTask.promise.then((pdf) => {
        this.pdfDoc = pdf;
        // this.pdf_pages = this.pdfDoc.numPages;
        // this.pdf_pages = this.pdf_pages >= 10 ? 10 : this.pdf_pages;
        if (numPages > 5) {
          this.pdf_pages = 10;
          //   this.$emit("eventNumPages", numPages);
        } else {
          this.pdf_pages = numPages;
        }
        this.$nextTick(() => {
          this._renderPage(1);
        });
      });
    },
    _renderPage(num) {
      //渲染pdf页
      const that = this;
      this.pdfDoc.getPage(num).then((page) => {
        let canvas = document.getElementById("the_canvas" + num);
        let ctx = canvas.getContext("2d");
        let dpr = window.devicePixelRatio || 1;
        let bsr =
          ctx.webkitBackingStorePixelRatio ||
          ctx.mozBackingStorePixelRatio ||
          ctx.msBackingStorePixelRatio ||
          ctx.oBackingStorePixelRatio ||
          ctx.backingStorePixelRatio ||
          1;
        let ratio = dpr / bsr;
        let viewport = page.getViewport({ scale: this.pdf_scale });
        canvas.width = viewport.width * ratio;
        canvas.height = viewport.height * ratio;
        canvas.style.width = viewport.width + "px";
        that.pdf_div_width = viewport.width + "px";
        canvas.style.height = viewport.height + "px";
        ctx.setTransform(ratio, 0, 0, ratio, 0, 0);
        let renderContext = {
          canvasContext: ctx,
          viewport: viewport,
        };
        page.render(renderContext);
        if (this.pdf_pages > num) {
          this._renderPage(num + 1);
        }
      });
    },
  },
};
</script>
<style scoped>
.home_wrap {
  width: 100%;
  height: 100%;
}
.pdf_down {
  position: fixed;
  display: flex;
  z-index: 20;
  right: 26px;
  bottom: 7%;
}
.pdf_set_left {
  width: 30px;
  height: 40px;
  color: #408fff;
  font-size: 11px;
  padding-top: 25px;
  text-align: center;
  margin-right: 5px;
  cursor: pointer;
}
.pdf_set_middle {
  width: 30px;
  height: 40px;
  color: #408fff;
  font-size: 11px;
  padding-top: 25px;
  text-align: center;
  margin-right: 5px;
  cursor: pointer;
}
</style>
  