<template>
  <div class="hello">
    <div class="header">
      <div
        class="btn"
        :class="{ active: componentName === 'PdfCanvas' }"
        @click="handleClick('PdfCanvas')"
      >
        pdf转canvas渲染
      </div>
      <div
        class="btn"
        :class="{ active: componentName === 'ImgCard' }"
        @click="handleClick('ImgCard')"
      >
        图片渲染
      </div>
      <div
        class="btn"
        :class="{ active: componentName === 'ImgCanvas' }"
        @click="handleClick('ImgCanvas')"
      >
        图片转canvas渲染
      </div>

      <div class="btn" v-if="!isVip" @click="handleClickVip">成为会员</div>
      <div class="txt" v-if="isVip">会员可查看全部</div>
    </div>

    <component :is="componentName" :images="images"></component>
    <div class="pages-index">{{ activeIndex }}/10</div>
  </div>
</template>

<script>
import ImgCard from "./ImgCard.vue";
import ImgCanvas from "./ImgCanvas.vue";
import PdfCanvas from "./PdfCanvas.vue";

export default {
  name: "HelloWorld",
  components: {
    ImgCard,
    ImgCanvas,
    PdfCanvas,
  },
  data() {
    return {
      componentName: "PdfCanvas",
      isVip: false,
      activeIndex: 5,
      images: [
        {
          imgUrl: require("../assets/images/01.jpg"),
        },
        {
          imgUrl: require("../assets/images/02.jpg"),
        },
        {
          imgUrl: require("../assets/images/03.jpg"),
        },
        {
          imgUrl: require("../assets/images/04.jpg"),
        },
        {
          imgUrl: require("../assets/images/05.jpg"),
        },
      ],
      images2: [
        {
          imgUrl: require("../assets/images/06.jpg"),
        },
        {
          imgUrl: require("../assets/images/07.jpg"),
        },
        {
          imgUrl: require("../assets/images/08.jpg"),
        },
        {
          imgUrl: require("../assets/images/09.jpg"),
        },
        {
          imgUrl: require("../assets/images/10.jpg"),
        },
      ],
    };
  },
  methods: {
    handleClick(name) {
      this.componentName = name;
    },
    handleClickVip() {
      this.isVip = true;
      this.images = [...this.images, ...this.images2];
      this.activeIndex = 10;
    },
  },
};
</script>
<style scoped>
.hello {
  background: #f5f5f5;
}
.header {
  width: 100%;
  display: flex;
  align-items: center;
  height: 80px;
  padding-left: 10px;
  box-sizing: border-box;
  position: fixed;
  background: #f5f5f5;
}
.btn {
  font-size: 12px;
  width: auto;
  padding: 12px 10px;
  border: 1px solid #f1f1f1;
  background: #fff;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
}
.active {
  background-color: burlywood;
}
.pages-index {
  width: 100%;
  height: 60px;
  position: fixed;
  bottom: 0;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
}
.txt {
  font-size: 12px;
  color: red;
  position: absolute;
  right: 10px;
}
</style>
